import React, { useEffect } from 'react'
import AOS from 'aos';
import 'aos/dist/aos.css';

import './NossaMissao.css'

const NossaMissao = () => {

    useEffect(() => {
        AOS.init({
            duration: 800,
            once: true
        });
    }, []);


    return (
        <section id="nossa-missao" className='flex'>


            <div className="nm-cards flex" data-aos="fade-right">
                <h3 className='mainTitle'>Nossa Misso</h3>

                <p>Oferecer solues integradas em Segurana, Servios e Tecnologia com qualidade que garanta ao cliente a tranquilidade necessria para cuidar de seu negcio, seu patrimnio e sua famlia.</p>
            </div>

            <div className="nm-cards flex" data-aos="fade-left">
                <h3 className='mainTitle'>Valores</h3>
                <ul>
                    <li>
                        tica
                    </li>
                    <li>
                        Comprometimento
                    </li>
                    <li>
                        Responsabilidade Social
                    </li>
                    <li>
                        Respeito
                    </li>
                    <li>
                        Qualidade
                    </li>


                </ul>
            </div>

        </section>
    )
}



export default NossaMissao