import React, { useState } from "react";
import ComumRoutes from "../../ComumVars/ComumRoutes.js"

// COMPONENTS
import { Box, Button, Container, IconButton, Menu, MenuItem, Typography, Link } from "@mui/material";

// ICONS
import ArrowRightIcon from '@mui/icons-material/ArrowRight';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import ArrowDropUpIcon from '@mui/icons-material/ArrowDropUp';
import FacebookIcon from '@mui/icons-material/Facebook';
import InstagramIcon from '@mui/icons-material/Instagram';
import XIcon from '@mui/icons-material/X';


// STYLES
import HeaderStyle from "./HeaderStyle.js";

const Header = () => {

    const [anchorElEmpresas, setAnchorElEmpresas] = useState(null);
    const [anchorElServicos, setAnchorElServicos] = useState(null);
    const [anchorElGrupo, setAnchorElGrupo] = useState(null);

    const openEmpresas = Boolean(anchorElEmpresas);
    const openServicos = Boolean(anchorElServicos);
    const openGrupo = Boolean(anchorElGrupo);

    const handleClickEmpresas = (event) => {
        setAnchorElEmpresas(event.currentTarget);
    };
    const handleCloseEmpresas = () => {
        setAnchorElEmpresas(null);
    };

    const handleClickServicos = (event) => {
        setAnchorElServicos(event.currentTarget);
    };
    const handleCloseServicos = () => {
        setAnchorElServicos(null);
    };

    const handleClickGrupo = (event) => {
        setAnchorElGrupo(event.currentTarget);
    };
    const handleCloseGrupo = () => {
        setAnchorElGrupo(null);
    };

    return (

        <Box sx={{ ...(HeaderStyle.Header) }} >

            <Box sx={{ ...(HeaderStyle.Logo) }}>
                <Link href={ComumRoutes.Grupo_Peres_Route} sx={{ ...(HeaderStyle.Link) }}>Logo</Link>
            </Box>

            <Box sx={{ ...(HeaderStyle.Navbar) }} >

                <Box sx={{ ...(HeaderStyle.NavbarItensContainer) }}>
                    <Box sx={{ ...(HeaderStyle.NavbarItem) }}>
                        <Button sx={{ ...(HeaderStyle.Link) }} onClick={()=> {}} >
                            HOME
                        </Button>
                    </Box>


                    <Box sx={{ ...(HeaderStyle.NavbarItem) }}>
                        <Button sx={{ ...(HeaderStyle.Link) }} onClick={handleClickGrupo} endIcon={openGrupo ? <ArrowDropUpIcon /> : <ArrowDropDownIcon />}>
                            O GRUPO
                        </Button>
                        <MenuContainer id={"grupo"} anchorEl={anchorElGrupo} open={openGrupo} handleClose={handleCloseGrupo} >
                            <MenuItens handleClose={handleCloseGrupo} itens={GRUPO} />
                        </MenuContainer>
                    </Box>

                    <Box sx={{ ...(HeaderStyle.NavbarItem) }}>
                        <Button sx={{ ...(HeaderStyle.Link) }} onClick={handleClickServicos} endIcon={openServicos ? <ArrowDropUpIcon /> : <ArrowDropDownIcon />}>
                            SERVIOS
                        </Button>
                        <MenuContainer id={"servicos"} anchorEl={anchorElServicos} open={openServicos} handleClose={handleCloseServicos} >
                            <MenuItens handleClose={handleCloseServicos} itens={SERVICOS} />
                        </MenuContainer>
                    </Box>

                    <Box sx={{ ...(HeaderStyle.NavbarItem) }}>
                        <Button sx={{ ...(HeaderStyle.Link) }} onClick={handleClickEmpresas} endIcon={openEmpresas ? <ArrowDropUpIcon /> : <ArrowDropDownIcon />}>
                            EMPRESAS
                        </Button>
                        <MenuContainer id={"empresas"} anchorEl={anchorElEmpresas} open={openEmpresas} handleClose={handleCloseEmpresas} >
                            <MenuItens handleClose={handleCloseEmpresas} itens={EMPRESAS} />
                        </MenuContainer>
                    </Box>



                </Box>

                <Button sx={{ ...(HeaderStyle.Orcamento) }} >
                    <Typography variant="subtitle2" color="text">
                        SOLICITE UM ORAMENTO
                    </Typography>
                </Button>



            </Box>
            <Box sx={{ ...(HeaderStyle.Social) }} >

                <IconButton color="text">
                    <FacebookIcon />
                </IconButton>

                <IconButton color="text">
                    <InstagramIcon />
                </IconButton>

                <IconButton color="text">
                    <XIcon />
                </IconButton>

            </Box>

        </Box >


    )
}






const MenuContainer = ({ id, anchorEl, open, handleClose, children }) => {
    return (
        <Menu
            id={id}
            anchorEl={anchorEl}
            open={open}
            onClose={handleClose}

        >
            {children}
        </Menu>
    );
}

const GRUPO = [
    // MUDAR AS ROTAS
    { route: ComumRoutes.Quem_Somos, label: 'QUEM SOMOS' },
    { route: ComumRoutes.Missao_Visao_Valores, label: 'MISSO, VISO E VALORES' },
    { route: ComumRoutes.Diferenciais, label: 'DIFERENCIAIS' },
];

const EMPRESAS = [
    { route: ComumRoutes.Peres_Seguranca, label: 'PERES SEGURANA' },
    { route: ComumRoutes.Genial, label: 'GENIAL SERVIOS' },
    { route: ComumRoutes.P3, label: 'P3 SEGURANA ELETRNICA' },
];

const SERVICOS = [
    // MUDAR AS ROTAS
    { route: ComumRoutes.Seguranca_Patrimonial, label: 'VIGILNCIA PATRIMONIAL' },
    { route: ComumRoutes.Seguranca_Eletronica, label: 'SEGURANA ELETRNICA' },
    { route: ComumRoutes.Eventos, label: 'TERCEIRIZAO DE MO DE OBRA' },
    { route: ComumRoutes.Servicos_Gerais, label: 'SERVIOS PARA EVENTOS' },
];

const MenuItens = ({ handleClose, itens }) => {
    return (
        <>
            {itens.map((item) => (
                <MenuItem onClick={handleClose} key={item.route}>
                    <Link href={item.route} sx={{ ...HeaderStyle.Link }}>{item.label}</Link>
                </MenuItem>
            ))}
        </>
    );
};

export default Header