import React from 'react'

import 'aos/dist/aos.css';

import "./Numeros.css"

const Numeros = () => {

  return (
    <section id="numeros" className='flex'>


      <div className="nossosNumeros_container flex" >

        <div className="numerosTitle">
          <h3 className='mainTitle'>Nossos Nmeros</h3>
        </div>

        <div className="nossosNumeros_content flex">

          <NumCard
            icon={<ion-icon name="accessibility-outline"></ion-icon>}
            num="456"
            title="algo"
          />

          <NumCard
            icon={<ion-icon name="accessibility-outline"></ion-icon>}
            num="456"
            title="algo"
          />

          <NumCard
            icon={<ion-icon name="accessibility-outline"></ion-icon>}
            num="456"
            title="algo"
          />

          <NumCard
            icon={<ion-icon name="accessibility-outline"></ion-icon>}
            num="456"
            title="algo"
          />

        </div>

      </div>

    </section>
  )
}

const NumCard = ({ icon, num, title }) => {

  return (
    <div className="num flex" data-aos="fade">

      <div className="numContent">
        {icon}
        {num}

      </div>

      <h3>{title}</h3>
    </div>
  )
}

export default Numeros