import { Routes, Route } from "react-router-dom";


// MUI
import { Box } from '@mui/material'

// COMPONENTS
import Header from '../components/Header/Header.jsx';
import Footer from '../components/Footer/Footer.jsx';

// PAGES
import Home from '../Pages/Homepage/Homepage.jsx'
import PeresSeguranca from '../Pages/PeresSeguranca/PeresSeguranca.jsx';
import Genialpage from '../Pages/Genial/Genialpage.jsx';
import P3page from '../Pages/P3/P3page.jsx';
import Contatos from '../components/ContactPage/Contatos.jsx';

import Seguranca_Patrimonial from '../Pages/Seguranca_Patrimomial/Seguranca_Patrimonial.jsx';


// FUNCTIONS
import ComumRoutes from "../ComumVars/ComumRoutes.js"

// STYLES
import AppStyle from "./AppStyle.js";


const App = () => {

    return (
        <>
            <Box sx={{
                display: 'flex',
                flexFlow: 'column'
            }}>

                <Header />

                <Routes>

                    <Route path={ComumRoutes.Grupo_Peres_Route} exact element={<Home />} />

                    <Route path={ComumRoutes.Peres_Seguranca} exact element={<PeresSeguranca />} />
                    <Route path={ComumRoutes.Genial} exact element={<Genialpage />} />
                    <Route path={ComumRoutes.P3} exact element={<P3page />} />

                    <Route path={ComumRoutes.Contatos} exact element={<Contatos />} />

                    <Route path={ComumRoutes.Seguranca_Patrimonial} exact element={<Seguranca_Patrimonial />} />
                    {/* <Route path={ComumRoutes.Seguranca_Eletronica} exact element={<Seguranca_Patrimonial />} />
                    <Route path={ComumRoutes.Eventos} exact element={<Seguranca_Patrimonial />} />
                    <Route path={ComumRoutes.Servicos_Gerais} exact element={<Seguranca_Patrimonial />} /> */}

                </Routes>

                <Footer />
            </Box>
        </>
    )
}

export default App