import React, { useState, useEffect } from 'react'

import AOS from 'aos';
import 'aos/dist/aos.css';

import Dep from './Dep/Dep'

import './Depoimentos.css'

const Depoimentos = () => {

    const [selectedItem, setSelectedItem] = useState(0);
    const totalItems = 2; // total number of slides

    const handleChange = (event) => {
        setSelectedItem(Number(event.target.value));
    };

    const handleLeftClick = () => {
        setSelectedItem(oldValue => (oldValue > 0 ? oldValue - 1 : totalItems - 1));
    };

    const handleRightClick = () => {
        setSelectedItem(oldValue => (oldValue < totalItems - 1 ? oldValue + 1 : 0));
    };

    const slideShow = {
        '--selected-item': selectedItem,
    };

    useEffect(() => {
        AOS.init({
            duration: 800,
            once: true
        });
    }, []);

    return (
        <section id="depoimentos" className='flex' data-aos="fade-up">

            <div id='depTitle'>
                <h3 className='mainTitle'>Reconhecimento</h3>
            </div>


            <div className='slide-show flex' style={slideShow} >
                <button id="button_left" className="slide_button flex" onClick={handleLeftClick}>
                    <ion-icon name="arrow-back-outline"></ion-icon>
                </button>

                <div className="slider-list flex">

                    <Dep img={""} name={"Matheus Ximenes"} cargo={"Presidente Empresa X"}
                        dep={"Desta maneira, a revoluo dos costumes estende o alcance e a importncia dos conhecimentos estratgicos para atingir a excelncia."}
                    ></Dep>


                    <Dep img={""} name={"Gustavo Souza"} cargo={"Diretor Empresa Y"}
                        dep={"Todas estas questes, devidamente ponderadas, levantam dvidas sobre se a contnua expanso de nossa atividade pode nos levar a considerar a reestruturao do levantamento das variveis envolvidas."}
                    ></Dep>
                </div>

                <button id="button_right" className="slide_button flex" onClick={handleRightClick}>
                    <ion-icon name="arrow-forward-outline"></ion-icon>
                </button>
            </div>

            <div className="slide-controller flex">
                <input type="radio" name="slide" value="0" checked={selectedItem === 0} onChange={handleChange} />
                <input type="radio" name="slide" value="1" checked={selectedItem === 1} onChange={handleChange} />
                {/* <input type="radio" name="slide" value="2" checked={selectedItem === 2} onChange={handleChange} />
                <input type="radio" name="slide" value="3" checked={selectedItem === 3} onChange={handleChange} /> */}
            </div>



        </section >
    )
}

export default Depoimentos