import { createTheme } from '@mui/material/styles';

const mainTheme = {
    // darker: "#1E2124",
    dark: "#2E3136",
    main: "#ea1c25",
    light: "#424549",
    // lighter: "#4A4C51",
}

const textTheme = {
    black: "#000000",
    white: "#FFFFFF",
}


const theme = createTheme({
    palette: {
        primary: {
            light: mainTheme.light,
            main: mainTheme.main,
            dark: mainTheme.dark,
            contrastText: '#fff',
        },

        secondary: {
            light: '#48484b',
            main: '#313135',
            dark: '#1a1a1e',
            contrastText: '#fff',
        },

        text: {
            main: textTheme.white,
            dark: textTheme.black,
        },
    },

    components: {
        MuiButton: {
            styleOverrides: {
                root: {
                    margin: '0',
                },
            },
        },

        MuiLink: {
            styleOverrides: {
                root: {
                    textDecoration: 'none',
                },
            },
        },


    },
});

export default theme;