import React from 'react'

import './Footer.css'

const Footer = () => {
    return (
        <footer>

            <div className="content flex">

                <div className="grupoPeres container flex">


                    <div className="matriz flex">

                        <div className="footer_icon">
                            <p>Grupo Peres</p>
                        </div>

                        <h3 className='footer_titles'>Matriz</h3>

                        <p>Rua Professor Eladio Magalhes, 1369</p>
                        <p>Edson Queiroz - Fortaleza - CE</p>
                        <p>CEP </p>

                    </div>

                </div>

                <div className="contatos_footer container flex">
                    <h3 className='footer_titles'>Contatos</h3>

                    <ul className='flex'>
                        <li className='flex'>
                            <ion-icon name="mail-outline"></ion-icon>
                            <p>peres@peresseguranca.com.br</p>
                        </li>

                        <li className='flex'>
                            <ion-icon name="call-outline"></ion-icon>
                            <p>85 3241-4654</p>
                        </li>

                    </ul>

                    <div id="redes-sociais-footer" className='socials-icons flex'>
                        <ion-icon name="logo-facebook"></ion-icon>

                        <ion-icon name="logo-instagram"></ion-icon>

                        <ion-icon name="logo-twitter"></ion-icon>
                    </div>


                </div>
            </div>

            <div className="divisor flex"></div>

            <div className="reservados flex">
                <p>
                    <span>Grupo Peres </span>
                     2024 - Todos os direitos reservados.
                </p>
            </div>

        </footer>
    )
}

export default Footer