import React from 'react'
import OrcamentoButton from '../BotaoOrcamento/OrcamentoButton'

import "./Main.css"

const Main = () => {
   
    return (
        <section id="main" className="main img flex">

            <div id="intro" className='flex'>
                <p>
                    Evidentemente, a competitividade nas transaes comerciais
                    obstaculiza a apreciao da importncia dos
                    procedimentos normalmente adotados.
                </p>

                <p>
                    Todas estas questes, devidamente ponderadas,
                    levantam dvidas sobre se a complexidade dos estudos
                    efetuados assume importantes posies no estabelecimento
                    dos paradigmas corporativos.
                </p>

                <OrcamentoButton />
            </div>

        </section>
    )
}

export default Main