import { Height, Scale, Translate } from "@mui/icons-material";

const HeaderStyle = {
    Header: {
        width: '100%',
        height: '80px',

        padding: '15px var(--default-padding)',

        position: 'absolute',
        top: 0,
        left: 0,
        zIndex: 2,

        display: 'flex',
        justifyContent: 'space-between',

        backgroundColor: 'transparent',
        fontWeight: 'bold',

        transition: 'all 0.5s ease',
    },

    Logo: {
        backgroundColor: 'black',
        padding: '10px',
    },

    Navbar: {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'space-between',
        alignItems: 'center',
        gap: '10px',

        borderRadius: '10px',

        fontWeight: 'bolder',
        textAlign: 'center',
        backgroundColor: 'white',
        transition: 'all 0.5s ease',
        paddingLeft: '15px',
    },

    NavbarOpen: {
        display: 'flex',
    },

    NavbarItensContainer:{
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'space-between',
        alignItems: 'center',
    },

    NavbarItem: {
        padding: '5px 15px',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
        position: 'relative',
    },

    Link: {
        color: '#000',
        textTransform: 'uppercase',
        fontWeight: 'bold',

        "&:hover": {
            backgroundColor: 'transparent',
            color: 'primary.main',
            transition: 'all 0.3s ease',
        },

        
        "&:hover .MuiButton-endIcon": {
            transition: 'all 0.3s ease',
            translate: '0px -2px',
        }
    },

    Orcamento: {
        width: '200px',
        height: '100%',
        padding: '5px 15px',

        backgroundColor: 'primary.main',
        color: 'white',
        borderRadius: '10px',
        fontWeight: 'bold',

        transition: 'all 0.5s ease',
        '&:hover': {
            scale: '1.05',
            backgroundColor: 'primary.dark',
        },
    },

    Social: {
        
        height: '100%',
        
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'center',
        alignItems: 'center',
        gap: '15px',

        '& .MuiIconButton-root:hover': {
            backgroundColor: 'primary.main',
        },
    },
};

export default HeaderStyle;
