import React, { useEffect } from 'react';
import './Apresentacao.css'
import AOS from 'aos';
import 'aos/dist/aos.css';

const Apresentacao = () => {

    useEffect(() => {
        AOS.init({
            duration: 800,
            once: true
        });
    }, []);

    return (
        <section id="apresentacao" className="img flex" >

            <div id="ap-texto" data-aos="fade-up">

                <h1>GRUPO PERES, a melhor escolha</h1>

                <p>
                    Com quase duas dcadas de atuao no Cear,
                    o Grupo Peres  a escolha ideal para a sua segurana patrimonial.
                    A nossa credibilidade e eficincia, especialmente na Peres Segurana,
                    so reconhecidas no mercado. Alm disso, a confiana que os
                    consumidores depositam em ns  um testemunho da nossa
                    confiabilidade. Escolher o Grupo Peres  optar por um
                    servio confivel, de qualidade e slido. Estamos  sua
                    disposio para atender s suas necessidades.
                </p>

            </div>


            <div id="ap-img" className="img" data-aos="fade-up"></div>

        </section>
    )
}

export default Apresentacao